@import '../../../../sass/variables.scss';

.animated{
    display: flex;
    &:nth-child(even) .Project{
        justify-content: flex-start !important;
        .Frame{
            margin-right: 15vw;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }
    &:nth-child(odd) .Project{
        justify-content: flex-end  !important;
        .Frame{
            margin-left: 15vw;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }
}

.Project{
    display: flex;
    width: 100%;
    min-height: 55vh;
    margin: 2.5vh 0;
    
    h1{
        padding-left: 40px;
    }
    .img{
        position: relative;
        margin: 20px 40px;
        height: 30vh;
        min-height: 20vh;
        .preview{
            display: none;
        }
        &:hover{
            .preview{
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: grey;
            opacity: 0.8;
            z-index: 1;
            box-sizing: border-box;
            border-radius: 15px;
            border: 5px double $fontcolor;
            .Icon{
                .svg {
                    background: rgba(100, 100, 100, 0.9);
                    width: 50px;
                    height: 50px;
                    opacity: 1;
                }
            }
            
            }
        }
        & > img {
            position: relative;
            box-sizing: border-box;
            padding: 10px;
            border: 5px double $fontcolor;
            border-radius: 15px;
            width: 100%;
            height: 100%;
            
        }
    }
    .content-col{
        padding: 10px;
        display: flex;
        flex-direction: column;
        
        h4{
            border: none;
            padding: 0;
        }
        .about{
            p{
                font-size: $fontlittle;
            }
        }
        .technologies{
            .Icon {
                    transform: scale(0.7);
                &:hover{
                    transform: scale(0.8);
                    
                }
            }
            
        }

    }
    
}